.navbar {
  display: flex;
  justify-content: space-around;
  width: 50%;
  margin-top: 2%;
}

.navElement {
  list-style-type: none;
  padding: 10px 20px;
  height: 100%;
  font-weight: 500;
  color: #fff;
  text-transform: uppercase;
}

.navElement:hover {
  background: orange;
  transition: 0.5s;
  cursor: pointer;
}
