@import url(https://fonts.googleapis.com/css?family=Merriweather&display=swap);
@import url(https://fonts.googleapis.com/css?family=Merriweather&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

#cover {
  background-color: rgb(43, 39, 28);
}

.grid {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.col-1-of-3 {
  width: 25%;
  height: 100%;
}

.col-1-of-4 {
  width: 20%;
  text-align: center;
  height: 100%;
}

.btnWithBorder {
  padding: 20px 40px;
  background: transparent;
  color: #fff;
  font-weight: 1000;
  border: 5px solid #696060;
}

.btnWithBorder:hover {
  cursor: pointer;
  background: #262626;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.btnNoBorder {
  border: none;
  background-color: transparent;
  color: rgb(204, 169, 133);
  font-size: 120%;
  font-family: "Merriweather", serif;
  padding: 0;
}

.btnNoBorder:hover {
  cursor: pointer;
}

.slider {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: -webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(26, 21, 21, 0.5)),
      to(rgba(65, 62, 62, 0.7))
    ),
    url(/static/media/slide_img_2.b29947a1.jpg);
  background: linear-gradient(
      to bottom,
      rgba(26, 21, 21, 0.5),
      rgba(65, 62, 62, 0.7)
    ),
    url(/static/media/slide_img_2.b29947a1.jpg);
  background-size: cover;
  background-attachment: fixed;
}

.slider_header {
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: space-between;
}

.slider_heading {
  margin: 0;
  padding: 20px;
  color: #fff;
}

.main_slider {
  width: 80%;
  margin: 5% auto;
  display: flex;
  height: 80%;
  text-align: center;
  color: #fff;
  align-items: center;
}

.logo {
  background-repeat: no-repeat;
  background-size: cover;
  height: 40%;
  width: 20%;
  margin: 0 auto;
}

.caption_big {
  font-size: 500%;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 5px;
}

.fas:hover {
  cursor: pointer;
}

.fa-arrow-left {
  font-size: 200%;
  margin-left: -10%;
  margin-right: 10%;
}

.fa-arrow-right {
  font-size: 200%;
  margin-left: 10%;
  margin-right: -10%;
}

.navbar {
  display: flex;
  justify-content: space-around;
  width: 50%;
  margin-top: 2%;
}

.navElement {
  list-style-type: none;
  padding: 10px 20px;
  height: 100%;
  font-weight: 500;
  color: #fff;
  text-transform: uppercase;
}

.navElement:hover {
  background: orange;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  cursor: pointer;
}

.textContainer {
  padding-top: 2.5%;
  text-align: center;
}

.textContainer__h3 {
  font-family: "Merriweather", serif;
  color: orange;
}

.textContainer__h1 {
  text-transform: uppercase;
  position: relative;
  color: #fff;
}

.textContainer__h1::after {
  position: absolute;
  content: "";
  width: 15%;
  height: 3px;
  background: orange;
  bottom: -50%;
  left: 43%;
}

.order {
  width: 80%;
  height: 30vh;
  margin: 5% auto;
  text-align: center;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: "";
}

input[type="date"]::-webkit-inner-spin-button {
  display: none;
  -webkit-appearance: none;
}

input[type="date"] {
  background: black;
  color: #fff;
  width: 15%;
  border: none;
  text-align: center;
  font-size: 120%;
  height: 8vh;
}

.order_submit {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-top: 5%;
}

.selectBox ::-ms-expand {
  display: none;
}

.selectBox {
  width: 15%;
  background: black;
  color: #fff;
  text-align: center;
  -moz-text-align-last: center;
       text-align-last: center;
  font-size: 120%;
  border: none;
  height: 8vh;
}

#btnOrder {
  width: 20%;
  background-color: rgb(92, 66, 39);
  border: 2px solid rgb(113, 81, 48);
  color: #fff;
  text-align: center;
  font-weight: 1000;
  letter-spacing: 2px;
}

#btnOrder:hover {
  cursor: pointer;
  background-color: rgb(48, 39, 28);
  -webkit-transition: 1s;
  transition: 1s;
}

.IntroImg {
  max-width: 100%;
  height: auto;
}

.IntroHeading {
  display: flex;
  align-items: center;
  margin: 0 auto;
  width: 95%;
  text-transform: uppercase;
  color: #fff;
  margin: 0;
}

.introNumber {
  margin-right: 5%;
  font-family: "Merriweather", serif;
  color: rgb(204, 169, 133);
  font-size: 300%;
}

.IntroText {
  color: #fff;
}

.sub_banner {
  width: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.4)), to(rgba(0, 0, 0, 0.4))),
    url(/static/media/slide_img_3.c14d69b5.jpg);
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url(/static/media/slide_img_3.c14d69b5.jpg);
  background-size: cover;
  background-position: -25px -86%;
  height: 70vh;
  margin-top: 5%;
  background-attachment: fixed;
}

.sub_banner_img {
  max-width: 20%;
  max-height: 40%;
}

.main_content {
  width: 80%;
  display: flex;
  align-items: center;
  padding: 10%;
  justify-content: space-between;
}

.mainText {
  width: 50%;
  color: #fff;
}

.text {
  color: #ffffff;
}

.coffeeMaker {
  margin-top: 5%;
}

.reviews {
  width: 80%;
  display: flex;
  height: 70vh;
  margin: 5% auto;
}

.imgContainer {
  width: 40%;
  height: 100%;
}

.reviewImg {
  max-width: 100%;
  height: 80%;
  background: -webkit-gradient(linear, right top, left top, from(rgba(0, 0, 0, 0.6)), to(rgba(0, 0, 0, 0.8)));
  background: linear-gradient(to left, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.8));
}

.mainTextReview {
  width: 50%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: 1px solid #fff;
  box-shadow: 0px 10px 20px #fff;
  color: #fff;
}

.reviews_para {
  width: 90%;
  margin: 5% auto;
}

.drinkItem {
  display: flex;
  color: #fff;
  width: 40%;
  justify-content: space-around;
}

.menuDescription {
  display: flex;
  flex-direction: column;
}

.drinkImg {
  float: left;
}

.menu {
  height: 100vh;
  width: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.6)), to(rgba(0, 0, 0, 0.7))),
    url(/static/media/slide_img_3.c14d69b5.jpg);
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.7)),
    url(/static/media/slide_img_3.c14d69b5.jpg);
  background-size: cover;
  background-attachment: fixed;
  overflow-x: hidden;
}

.listContainer {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 100%;
  width: 100%;
  justify-content: space-between;
}

::-webkit-scrollbar {
  display: none;
}

.contact {
  height: 50vh;
  width: 100%;
}

