.menu {
  height: 100vh;
  width: 100%;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.7)),
    url("../img/slide_img_3.jpg");
  background-size: cover;
  background-attachment: fixed;
  overflow-x: hidden;
}

.listContainer {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 100%;
  width: 100%;
  justify-content: space-between;
}

::-webkit-scrollbar {
  display: none;
}
