@import url("https://fonts.googleapis.com/css?family=Merriweather&display=swap");

.textContainer {
  padding-top: 2.5%;
  text-align: center;
}

.textContainer__h3 {
  font-family: "Merriweather", serif;
  color: orange;
}

.textContainer__h1 {
  text-transform: uppercase;
  position: relative;
  color: #fff;
}

.textContainer__h1::after {
  position: absolute;
  content: "";
  width: 15%;
  height: 3px;
  background: orange;
  bottom: -50%;
  left: 43%;
}
