.slider {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: linear-gradient(
      to bottom,
      rgba(26, 21, 21, 0.5),
      rgba(65, 62, 62, 0.7)
    ),
    url("../img/slide_img_2.jpg");
  background-size: cover;
  background-attachment: fixed;
}

.slider_header {
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: space-between;
}

.slider_heading {
  margin: 0;
  padding: 20px;
  color: #fff;
}

.main_slider {
  width: 80%;
  margin: 5% auto;
  display: flex;
  height: 80%;
  text-align: center;
  color: #fff;
  align-items: center;
}

.logo {
  background-repeat: no-repeat;
  background-size: cover;
  height: 40%;
  width: 20%;
  margin: 0 auto;
}

.caption_big {
  font-size: 500%;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 5px;
}

.fas:hover {
  cursor: pointer;
}

.fa-arrow-left {
  font-size: 200%;
  margin-left: -10%;
  margin-right: 10%;
}

.fa-arrow-right {
  font-size: 200%;
  margin-left: 10%;
  margin-right: -10%;
}
