.order {
  width: 80%;
  height: 30vh;
  margin: 5% auto;
  text-align: center;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: "";
}

input[type="date"]::-webkit-inner-spin-button {
  display: none;
  -webkit-appearance: none;
}

input[type="date"] {
  background: black;
  color: #fff;
  width: 15%;
  border: none;
  text-align: center;
  font-size: 120%;
  height: 8vh;
}

.order_submit {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-top: 5%;
}

.selectBox ::-ms-expand {
  display: none;
}

.selectBox {
  width: 15%;
  background: black;
  color: #fff;
  text-align: center;
  text-align-last: center;
  font-size: 120%;
  border: none;
  height: 8vh;
}

#btnOrder {
  width: 20%;
  background-color: rgb(92, 66, 39);
  border: 2px solid rgb(113, 81, 48);
  color: #fff;
  text-align: center;
  font-weight: 1000;
  letter-spacing: 2px;
}

#btnOrder:hover {
  cursor: pointer;
  background-color: rgb(48, 39, 28);
  transition: 1s;
}
