.reviews {
  width: 80%;
  display: flex;
  height: 70vh;
  margin: 5% auto;
}

.imgContainer {
  width: 40%;
  height: 100%;
}

.reviewImg {
  max-width: 100%;
  height: 80%;
  background: linear-gradient(to left, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.8));
}

.mainTextReview {
  width: 50%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: 1px solid #fff;
  box-shadow: 0px 10px 20px #fff;
  color: #fff;
}

.reviews_para {
  width: 90%;
  margin: 5% auto;
}
