.sub_banner {
  width: 100%;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url("../img/slide_img_3.jpg");
  background-size: cover;
  background-position: -25px -86%;
  height: 70vh;
  margin-top: 5%;
  background-attachment: fixed;
}

.sub_banner_img {
  max-width: 20%;
  max-height: 40%;
}

.main_content {
  width: 80%;
  display: flex;
  align-items: center;
  padding: 10%;
  justify-content: space-between;
}

.mainText {
  width: 50%;
  color: #fff;
}
