.btnWithBorder {
  padding: 20px 40px;
  background: transparent;
  color: #fff;
  font-weight: 1000;
  border: 5px solid #696060;
}

.btnWithBorder:hover {
  cursor: pointer;
  background: #262626;
  transition: 0.5s;
}

.btnNoBorder {
  border: none;
  background-color: transparent;
  color: rgb(204, 169, 133);
  font-size: 120%;
  font-family: "Merriweather", serif;
  padding: 0;
}

.btnNoBorder:hover {
  cursor: pointer;
}
